.LandingPage-customer-count-list {
  list-style: none;
  display: flex;
  flex-flow: wrap;
  padding: 0;
  margin: 0;
  justify-content: center;
}

.LandingPage-customer-count-list li {
  width: 20px;
  height: 20px;
  text-align: center;
  margin: 5px;
  border: 2px solid;
  border-radius: .2em;
  cursor: pointer;
}

.LandingPage-customer-count-list li.active {
  color: red;
}
